<template>
    <section id="section-end" :class="endbg">
        <div class="end-block">
            <div class="end-text">
                <div class="title">
                    <h4 v-if="endtitle">{{endtitle}}</h4>
                    <p v-if="endcontent">{{endcontent}}</p>
                </div>
                <a class="again">
                    <h5><img src="../assets/img/return.png" alt=""> 再玩一次</h5>
                    <p>聽聽看不同曲目的音樂會吧!</p>
                </a>
            </div>
            <div class="end-links">
                <div class="right">
                    <button class="link how" @click="gameopen">好感度攻略！</button>
                    <a class="link search">來場音樂會約會吧</a>
                    <a href="" class="fb"><img src="../assets/img/fb.png" alt=""> 分享遊戲</a>
                </div>
                <div class="left">
                    <a href="" class="muzik"><img src="../assets/img/muzik.jpg" alt="" width="115" height="115"></a>
                    <button class="link staff"  @click="staffopen">製作名單</button>
                </div>
            </div>
        </div>
        <div id="gamewindow" class="answer-block">
            <div class="cross" @click="gameclose"><img src="../assets/img/cross.png" alt=""></div>
            <div class="game_answer">
                <div class="question_outer" v-for="item in lan_file.game" :key="item.id" :class="{done:item.doneclass}">
                    <div class="img"><img :src="item.imgurl" alt=""></div>
                    <div class="content">
                        <h6>{{item.question}}</h6>
                        <p>{{item.content}}</p>
                    </div>
                </div>
            </div>
        </div>
        <div id="staffwindow" class="staff-block" @click="staffclose">
            <div class="cross" ><img src="../assets/img/cross.png" alt=""></div>
            <h6>製作名單</h6>
            <div class="text">
                <div class="left">
                    <p>製作單位</p>
                    <P>美術設計</P>
                    <P>網頁程式</P>
                </div>
                <div class="right">
                    <p>muzik 音樂雜誌</p>
                    <p>mia 曾敏雅</p>
                    <p>高巧蓉</p>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
export default {
    name:'SectionEnd',
    props:['lan','heart','gameid'],
