                    '兩人的觀念不同，先中場休息，暫時劃下休止符吧！'],
                game:[
                    {
                        id: 0,
                        question: '喉嚨突然有點癢，現在音樂蠻大聲的，雖然有喉糖但是從包包拿出來會有聲音，還是咳出來就好吧？',
                        content:'偶爾的輕咳或是噴嚏無傷大雅，如果是連續性的咳嗽記得要自備喉糖或是溫水等以免影響其他觀眾喔！',
                        imgurl: require('../assets/img/small_throat.png'),
                        doneclass: false
                    },
                    {
                        id: 1,
                        question: '這段演奏好動人，拍一張照回去做紀念！',
                        content:'在音樂會進行中圖拍照、錄影、錄音都是不允許的，在音樂廳開場前的廣播會說明能否在「謝幕時」拍照喔',
                        imgurl: require('../assets/img/small_phone.png'),
                        doneclass: false
                    },
                    {
                        id: 2,
                        question: '突然有一通電話要回覆，要怎麼辦呢？',
                        content:'如果不是緊急事件，請等到音樂會休息或是結束後再使用。',
                        imgurl: require('../assets/img/small_door.png'),
                        doneclass: false
                    },
                    {
                        id: 3,
                        question: '是不是該把手機鈴聲開啟，以防專心聽音樂會時也不會漏接電話',
                        content:'手機的鈴聲、震動、亮光等都會讓身旁的人受到影響，盡量轉靜音來保持安靜喔',
                        imgurl: require('../assets/img/small_phone.png'),
                        doneclass: false
