                        imgurl: require('../assets/img/small_talk.png'),
                        doneclass: false
                    },
                    {
                        id: 5,
                        question: '坐下後要將隨身物品放在哪裡比較好呢？',
                        content:'將隨身物品（例如：包包、手機、節目冊等等）放在位置下方，以免中途掉落發出太大的聲響。',
                        imgurl: require('../assets/img/small_drop.png'),
                        doneclass: false
                    },
                    {
                        id: 6,
                        question: '旁邊有人要獻花，這件事是被允許的嗎？',
                        content:'有些音樂會謝幕時都是由工作人員獻花給演奏者，不妨在音樂會大廳詢問櫃台人員，請他們代為轉交花束！',
                        imgurl: require('../assets/img/small_flower.png'),
                        doneclass: false
                    },
                    {
                        id: 7,
                        question: '啊！指揮轉過身來要大家把手舉起來打節奏呢！',
                        content:'除非指揮有指示觀眾鼓掌打節奏，否則忘情地打節奏或跟著哼旋律可是會大大影響旁邊的聽眾的！',
                        imgurl: require('../assets/img/small_tempo.png'),
                        doneclass: false
                    },
                    {
                        id: 8,
                        question: '這段真的太精彩了，一定要及時給予鼓勵！',
                        content:'一般來說在樂章中間不鼓掌，最「保險」的鼓掌時機就是等指揮轉過身舉起手，或是其他觀眾鼓掌時再動作！',
                        imgurl: require('../assets/img/small_clap.png'),
                        doneclass: false
                    }
                ]
            },
            lan_en:{
                end_title:['EN(延長記號）','EN（反覆記號）','EN（休止符）'],
                end_content:[
                    'EN兩人渡過了愉快的一晚，也為這場音樂會的美好音符畫上了永恆的延長記號',
                    'EN兩人的觀念不盡相同，卻激起了更多討論，接下來的約會能有幾次反覆呢？',
                    'EN兩人的觀念不同，先中場休息，暫時劃下休止符吧！'],
                game:[
                    {
                        id: 0,
                        question: 'EN喉嚨突然有點癢，現在音樂蠻大聲的，雖然有喉糖但是從包包拿出來會有聲音，還是咳出來就好吧？',
                        content:'00解釋答案說明放這裡解釋答案說明放這裡解釋答案說明放這裡',
                        imgurl: require('../assets/img/small_throat.png'),
                        doneclass: false
                    },
                    {
                        id: 1,
                        question: 'EN這段演奏好動人，偷偷拍一張照回去做紀念！',
                        content:'01解釋答案說明放這裡解釋答案說明放這裡解釋答案說明放這裡',
                        imgurl: require('../assets/img/small_phone.png'),
                        doneclass: false
                    },
                    {
                        id: 2,
                        question: 'EN突然有一通電話要回覆，要怎麼辦呢？',
                        content:'02解釋答案說明放這裡解釋答案說明放這裡解釋答案說明放這裡',
                        imgurl: require('../assets/img/small_door.png'),
                        doneclass: false
                    },
                    {
                        id: 3,
                        question: 'EN是不是該把手機鈴聲開啟，以防專心聽音樂會時也不會漏接電話',
                        content:'03解釋答案說明放這裡解釋答案說明放這裡解釋答案說明放這裡',
                        imgurl: require('../assets/img/small_phone.png'),
                        doneclass: false
                    },
                    {
                        id: 4,
                        question: 'EN這段有點聽不懂，要問心儀對象嗎？',
                        content:'04解釋答案說明放這裡解釋答案說明放這裡解釋答案說明放這裡',
                        imgurl: require('../assets/img/small_talk.png'),
                        doneclass: false
                    },
                    {
                        id: 5,
                        question: 'EN坐下後要將隨身物品放在哪裡比較好呢？',
                        content:'05解釋答案說明放這裡解釋答案說明放這裡解釋答案說明放這裡',
                        imgurl: require('../assets/img/small_drop.png'),
                        doneclass: false
                    },
                    {
                        id: 6,
                        question: 'EN旁邊有人要獻花，這件事是被允許的嗎？',
                        content:'06解釋答案說明放這裡解釋答案說明放這裡解釋答案說明放這裡',
                        imgurl: require('../assets/img/small_flower.png'),
                        doneclass: false
                    },
                    {
                        id: 7,
                        question: 'EN啊！指揮轉過身來要大家把手舉起來打節奏呢！',
                        content:'07解釋答案說明放這裡解釋答案說明放這裡解釋答案說明放這裡',
                        imgurl: require('../assets/img/small_tempo.png'),
                        doneclass: false
                    },
                    {
                        id: 8,
                        question: 'EN這段真的太精彩了，一定要及時給予鼓勵！',
                        content:'08解釋答案說明放這裡解釋答案說明放這裡解釋答案說明放這裡',
                        imgurl: require('../assets/img/small_clap.png'),
                        doneclass: false
                    }
                ]
            }
        }
    },
    mounted(){
        if(this.lan == 'ch'){
            this.lan_file = this.lan_ch;
            console.log(this.lan_file)
        }else{
            this.lan_file = this.lan_en;
        }

        if(this.heart>9){
            this.endtitle = this.lan_file.end_title[0];
            this.endcontent = this.lan_file.end_content[0];
            this.$parent.$emit('updatemusicid', 8);
        }else if(this.heart>6 && this.heart<=9){
            this.endtitle = this.lan_file.end_title[1];
            this.endcontent = this.lan_file.end_content[1];
            this.$parent.$emit('updatemusicid', 9);
        }else{
            this.endtitle = this.lan_file.end_title[2];
            this.endcontent = this.lan_file.end_content[2];
            this.$parent.$emit('updatemusicid', 10);
        }
        
        for(let i=0;i<this.gameid.length;i++){
            var j = this.gameid[i];
            this.lan_file.game[j].doneclass = true;
            console.log(j)
        }
    },
    methods:{
        gameopen(){
            var gamewindow = document.getElementById("gamewindow");
            gamewindow.classList.add('show');
        },
        gameclose(){
            var gamewindow = document.getElementById("gamewindow");
            gamewindow.classList.remove('show');
        },
        staffopen(){
            var gamewindow = document.getElementById("staffwindow");
            gamewindow.classList.add('show');
        },
        staffclose(){
            var gamewindow = document.getElementById("staffwindow");
            gamewindow.classList.remove('show');
        }
    },
    computed:{
        endbg:function(){
            if(this.heart>9){
                return 'goodbg';
            }else if(this.heart>6 && this.heart<=9){
                return 'normalbg';
            }else{
                return 'badbg';
            }
        }
    }
}
</script>
<style>
#section-end p{
    margin: 0;
}
#section-end.goodbg{
    background-image: url(../assets/img/bg_end_good.jpg);
}
#section-end.normalbg{
    background-image: url(../assets/img/bg_end_normal.jpg);
}
#section-end.badbg{
    background-image: url(../assets/img/bg_end_bad.jpg);
}
#section-end a,
#section-end a:hover{
    text-decoration: none;
}
#section-end .end-block{
    position: absolute;
    width: 340px;
    height: calc(100% - 50px);
    right: 30px;
    top: 25px;
}
#section-end .end-block .end-text{
    background-color: #fff;
    border-radius: 20px;
    overflow: hidden;
}
#section-end .end-block .end-text .title{
    padding: 15px;
    font-weight: bold;
    background-color: #f0f0f0;
}
#section-end .end-block .end-text .title h4{
    margin: 0;
    margin-bottom: 20px;
    font-size: 30px;
    color: #ea6e24;
    text-align: center;
}
#section-end .end-block .end-text .title p{
    font-weight: bold;
    font-size: 22px;
    color: #414141;
}
#section-end .end-block .end-text .again{
    display: block;
    padding: 20px;
    text-align: center;
}
#section-end .end-block .end-text .again h5{
    margin: 0;
    font-weight: bold;
    font-size: 30px;
}
#section-end .end-block .end-text .again h5 img{
    vertical-align: unset;
    width: 26px;
}
#section-end .end-block .end-text .again p{
    font-size: 22px;
    font-weight: bold;
    color: #414141;
}
#section-end .end-block .end-links{
    display: flex;
    padding: 10px 0;
}
#section-end .end-block .end-links .right{
    width: 215px;
    padding: 5px;
}
#section-end .end-block .end-links .left{
    width: 125px;
    padding: 5px;
}

#section-end .end-block .end-links .right .link{
    display: block;
    width: 100%;
    padding: 10px;
    margin-bottom: 12px;
    font-size: 22px;
    font-weight: bold;
    color: #414141;
    background-color: #ffffff;
    border: none;
    border-radius: 15px;
    box-shadow: inset 0 0 10px #cbc9c8;
    text-align: center;
}
#section-end .end-block .end-links .right .fb{
    display: block;
    position: relative;
    background-color: #65a1df;
    color: #FFF;
    text-align: center;
    font-size: 18px;
    padding: 13px;
    padding-left: 60px;
    border-radius: 15px;
    overflow: hidden;
    font-weight: bold;
    letter-spacing: 2px;
}
#section-end .end-block .end-links .right .fb img{
    position: absolute;
    left: 0;
    top: 0;
    height: 50px;
    width: 50px;
}
#section-end .end-block .end-links .left .muzik{
    display: block;
    width: 115px;
    height: 115px;
    margin-top: 0px;
    margin-bottom: 8px;
    border-radius: 15px;
    overflow: hidden;
}
#section-end .end-block .end-links .left .link{
    display: block;
    width: 100%;
    padding: 14px;
    letter-spacing: 2px;
    font-size: 18px;
    text-align: center;
    font-weight: bold;
    color: #ffffff;
    background-color: #4c4c4c;
    /* border: 3px solid #c9c9c9; */
    border-radius: 15px;
    text-shadow: 0 1px 4px #592929;
    box-shadow: inset 0 0 10px #787878;
}
#section-end .answer-block{
    position: absolute;
    width: 100%;
    max-height: 0;
    height: 100%;
    top: -200px;
    background-color: rgba(42, 34, 31, 0.8);
    border-bottom-left-radius: 100%;
    border-bottom-right-radius: 100%;
    padding-bottom: 200px;
    opacity: 0;
    overflow: hidden;
    transition-duration: .3s;
}
#section-end .answer-block.show{
    max-height: 100%;
    top: 0;
    padding-bottom: 0;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    opacity: 1;
}
#section-end .answer-block .cross{
    position: absolute;
    z-index: 10;
    right: 165px;
    top: 15px;
    padding: 12px;
    background-color: #dd6a6a;
    border-radius: 100%;
    cursor: pointer;
}
#section-end .answer-block .cross img{
    width: 20px;
    height: 20px;
    vertical-align: bottom;
}
#section-end .answer-block .game_answer{
    position: absolute;
    width: 600px;
    height: 480px;
    top: 30px;
    left: 180px;
    background-color: rgba(42, 34, 31, 0.8);
    padding: 15px;
    overflow-y: scroll;
    border: 1px solid #a8a8a8;
    border-radius: 15px;
}
#section-end .answer-block .game_answer .question_outer{
    display: flex;
    padding: 10px;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 15px;
    margin-bottom: 10px;
    filter: grayscale(1) contrast(0.5);
}
#section-end .answer-block .game_answer .question_outer.done{
    filter: none;
}
#section-end .answer-block .game_answer .question_outer .img{
    border-radius: 100%;
    overflow: hidden;
    height: 100px;
    width: 100px;
    margin-right: 20px;
}
#section-end .answer-block .game_answer .question_outer .content{
    width: 400px;
}
#section-end .answer-block .game_answer .question_outer .content h6{
    margin: 0;
    margin-bottom: 15px;
    font-size: 22px;
}
#section-end .answer-block .game_answer .question_outer .content p{
    font-size: 20px;
}
#section-end .staff-block{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(42, 34, 31, 0.8);
    padding: 60px 80px;
    transition-duration: 0.3s;
    right: -100%;
}
#section-end .staff-block.show{
    right: 0;
}
#section-end .staff-block .cross{
    text-align: center;
}
#section-end .staff-block h6{
    font-size: 30px;
    margin: 0;
    margin-bottom: 35px;
}
#section-end .staff-block h6{
    margin: 20px 0;
    color: #FFF;
    text-align: center;
}
#section-end .staff-block .text{
    display: flex;
    justify-content: center;
    color: #FFF;
    width: 100%;
    line-height: 36px;
    font-size: 20px;
}
#section-end .staff-block .text .left{
    width: 50%;
    padding: 0 30px;
    border-right: 1px solid #FFF;
    text-align: right;
}
#section-end .staff-block .text .right{
    width: 50%;
    padding: 0 30px;
}
.bounce-enter-active {
    animation: bounce-in .5s;
}
.bounce-leave-active {
    animation: bounce-in .5s reverse;
}
@keyframes bounce-in {
    0% {
        transform: scale(0);
    }
    50% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
}
</style>