<template>
  <div id="app">
    <div id="suggest_block">
      <img src="./assets/img/infowindow.jpg" alt />
      <span>請橫放螢幕遊玩</span>
    </div>
    <div v-if="1==2">
      <p style="color:#FFF;">名字：{{name}}/遊戲階段：{{sectionid}}/對象性別:{{datesex}}</p>
      <p style="color:#FFF;">演奏會：{{concertname}} / 愛心數:{{heart}} / 玩過的遊戲id:{{gameid}}</p>
      <button @click="changeLan">switch language</button>
      <button @click="playappmusic">play</button>
      <button @click="changeappmusic(5)">switch</button>
    </div>
    <div class="main-view">
      <!-- section change start-->
      <loadsrc v-show="sectionid == 'load'" />
      <transition name="fade" mode="out-in" :duration="1000">
        <SectionStart v-if="sectionid == 'start'" :lan="language" />
        <SectionMeet v-if="sectionid == 'meet'" :name="name" :lan="language" />
        <SectionIntro
          v-if="sectionid == 'intro'"
          :name="name"
          :lan="language"
          :datesex="datesex"
          :concert="concert"
          :concertname="concertname"
        />
        <SectionGame
          v-if="sectionid == 'game'"
          :name="name"
          :lan="language"
          :datesex="datesex"
          :concert="concert"
          :concertname="concertname"
        />
        <SectionEnd v-if="sectionid == 'end'" :lan="language" :heart="heart" :gameid="gameid" />
      </transition>
      <!-- section change end-->
      <!-- 轉場用背景圖 -->
      <img
        src="./assets/img/bg02blur.jpg"
        alt
        v-if="sectionid == 'meet'"
        style="position:absolute;top:0;left:0;z-index:-1;"
      />
      <img
        :src="playbg.url"
        alt
        v-if="sectionid == 'game'"
        style="position:absolute;top:0;left:0;z-index:-1;"
      />
    </div>
    <div style="position:absolute;bottom:0">
      <audio :id="'appaudio'+sound.id" v-for="sound in soundarray" :key="sound.id" >
        <source :src="sound.mp3url" type="audio/mp3" />
      </audio>
    </div>
  </div>
</template>

    <script>
import loadsrc from "./components/loadsrc";
import SectionStart from "./components/SectionStart";
import SectionMeet from "./components/SectionMeet";
import SectionIntro from "./components/SectionIntro";
import SectionGame from "./components/SectionGame";
import SectionEnd from "./components/SectionEnd";

export default {
  name: "App",
  components: {
    loadsrc,
    SectionStart,
    SectionMeet,
    SectionIntro,
    SectionGame,
    SectionEnd
  },
  data: function() {
    return {
      screenWidth: document.body.clientWidth,
      screenHeight: document.body.clientHeight,
      language: "ch",
      name: "", //''
      sectionid: "", //''
      datesex: "", //''
      concert: "", //''
      concertname: "", //''
      playbg: "",
      heart: "",
      gameid: [],
      nowmusicid: 0,
      newmusicid: Number,
      nowmusicvolume: 1,
      pausemusic: "",
      playbgarray: [
        { url: require("./assets/img/play01.jpg") },
        { url: require("./assets/img/full2.jpg") },
        { url: require("./assets/img/full3.jpg") },
        { url: require("./assets/img/play04.jpg") }
      ],
      soundarray: [
        {
          id: 0,
          mp3url: require("./assets/mp3/bg_00start.mp3")
        },
        {
          id: 1,
          mp3url: require("./assets/mp3/bg_01love.mp3")
        },
        {
          id: 2,
          mp3url: require("./assets/mp3/bg_02noise.mp3")
        },
        {
          id: 3,
          mp3url: require("./assets/mp3/bg_03concert01.mp3")
        },
        {
          id: 4,
          mp3url: require("./assets/mp3/bg_04concert02.mp3")
        },
        {
          id: 5,
          mp3url: require("./assets/mp3/bg_05concert03.mp3")
        },
        {
          id: 6,
          mp3url: require("./assets/mp3/bg_06concert04.mp3")
        },
        {
          id: 7,
          mp3url: require("./assets/mp3/bg_07clap.mp3")
        },
        {
          id: 8,
          mp3url: require("./assets/mp3/bg_08endgood.mp3")
        },
        {
          id: 9,
          mp3url: require("./assets/mp3/bg_09endnormal.mp3")
        },
        {
          id: 10,
          mp3url: require("./assets/mp3/bg_10endbad.mp3")
        }
      ]
    };
  },
  methods: {
    suggesttoggle() {
      let suggestBlock = document.getElementById("suggest_block");
      let w = this.screenWidth;
      let h = this.screenHeight;
      if (h > w) {
        suggestBlock.classList.add("active");
      } else {
        suggestBlock.classList.remove("active");
      }
      // console.log(w +'px + '+ h +'px');
    },
    selfupdatename(val) {
      this.name = val;
    },
    selfupdatesection(val) {
      this.sectionid = val;
    },
    selfupdatesex(val) {
      this.datesex = val;
    },
    selfupdateconcert(val) {
      this.concert = val;
    },
    selfupdateheart(val) {
      this.heart = val;
    },
    selfupdategameid(val) {
      this.gameid = val;
    },
    selfupdatemusicid(val) {
      this.newmusicid = val;
      console.log(this.newmusicid);
    },
    changeLan() {
      this.language == "ch" ? (this.language = "en") : (this.language = "ch");
    },
    concertnamewrite() {
      if (this.concert == 1) {
        this.concertname = "室內樂";
      }
      if (this.concert == 2) {
        this.concertname = "交響曲";
      }
      if (this.concert == 3) {
        this.concertname = "協奏曲";
      }
      if (this.concert == 4) {
        this.concertname = "鋼琴獨奏";
      }
      console.log(this.concertname);
    },
    changeplaybg() {
      if (this.concert == 1) {
        this.playbg = this.playbgarray[0];
      } else if (this.concert == 2) {
        this.playbg = this.playbgarray[1];
      } else if (this.concert == 3) {
        this.playbg = this.playbgarray[2];
      } else if (this.concert == 4) {
        this.playbg = this.playbgarray[3];
      } else {
        console.log("no play bg in app.vue");
      }
    },
    playappmusic() {
      document.getElementById("appaudio" + this.nowmusicid).play();
      if(this.nowmusicid!==7){ //掌聲不用loop
      document.getElementById("appaudio" + this.nowmusicid).loop = true;
      }
    },
    pauseappmusic(value) {
      let nowsound = document.getElementById("appaudio" + this.nowmusicid);
      //let newsound = document.getElementById('appaudio'+this.newmusicid);
      console.log("now" + this.nowmusicid + "new" + this.newmusicid);
      // console.log(nowsound.volume);
      //console.log(newsound.volume);
      if (this.nowmusicid !== this.newmusicid) {
        var fadeoutAudio = setInterval(function() {
          if (nowsound.volume > 0.0) {
            nowsound.volume = Math.max(0, nowsound.volume - value);
          } else {
            clearInterval(fadeoutAudio);
            nowsound.pause();
          }
        }, 200);
      }
    },
    changeappmusic(id) {
      this.newmusicid = id;
      console.log(this.nowmusicid + "->" + this.newmusicid);
    },
    pausegamemusic() {
      let nowsound = document.getElementById("appaudio" + this.nowmusicid);

      var fadeoutAudio = setInterval(function() {
        if (nowsound.volume > 0.0) {
          nowsound.volume = Math.max(0, nowsound.volume - 0.05);
        } else {
          nowsound.pause();
          clearInterval(fadeoutAudio);
        }
      }, 100);
    }
  },
  mounted() {
    this.suggesttoggle();
    var _this = this;
    window.onresize = function() {
      // 定義視窗大小變更通知事件
      _this.screenWidth = document.documentElement.clientWidth; //視窗寬度
      _this.screenHeight = document.documentElement.clientHeight; //視窗高度
    };
    this.$on("updatename", this.selfupdatename); //接收更換名字
    this.$on("updatesection", this.selfupdatesection); //接收更換階段
    this.$on("updatesex", this.selfupdatesex); //接收對象性別
    this.$on("updateconcert", this.selfupdateconcert); //接收音樂會類型
    this.$on("updateheart", this.selfupdateheart); //接收愛心數
    this.$on("updategameid", this.selfupdategameid); //玩過的遊戲題目id
    this.$on("updatemusicid", this.selfupdatemusicid); //要撥放的音樂id
    this.$on("updategamemusicpause", this.pausegamemusic); //要撥放的音樂id
  },
  watch: {
    screenWidth() {
      this.suggesttoggle();
    },
    screenHeight() {
      this.suggesttoggle();
    },
    concert() {
      this.concertnamewrite();
    },
    sectionid() {
      this.changeplaybg();
    },
    newmusicid() {
      let nowsound = document.getElementById("appaudio" + this.nowmusicid);
      let newsound = document.getElementById("appaudio" + this.newmusicid);
      nowsound.volume = 1;
      newsound.volume = 1;
      this.pauseappmusic(0.2);
      console.log("wtf ");
      this.nowmusicid = this.newmusicid;
      this.playappmusic();
    }
  }
};
</script>

<style>
* {
  /* outline: 1px solid; */
  box-sizing: border-box;
  font-family: 微軟正黑體;
}
html,
body {
  margin: 0;
  padding: 0;
}
body {
  overflow-x: hidden;
  background-color: #222222;
}
button {
  padding: 0;
  cursor: pointer;
}
.btn {
  border: none;
}
#suggest_block {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  text-align: center;
  z-index: 10000;
}
#suggest_block img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
#suggest_block span {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  line-height: 100vh;
  font-size: 10vw;
  color: #fff;
  text-shadow: 0 0.2rem 1rem #b43b0e;
}

#suggest_block.active {
  display: block;
}
#app {
  width: 100vw;
  height: 100vh;
  position: relative;
}
.main-view {
  position: absolute;
  width: 960px;
  height: 540px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#section-start,
.section-meet,
#section-intro,
#section-game,
#section-end {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
</style>
