<template>
    <section id="section-start">
        <!-- stateid == 0  開始遊戲按鈕 -->
        <button v-if="stateid == 0" id="startbtn" class="btn btn-start" @click="startgame"></button>
        <!-- stateid == 1  輸入名字-->
        <div v-if="stateid == 1" class="name">
            <form action="" style="display:flex;">
                <input v-model="name" type="text" id="nameinput" :class="{'send':name!==''}" placeholder="輸入名字">
                <transition name="fade">
                    <button v-show="name!==''" class="btn btn-name" @click="sendname"> </button>
                </transition>
            </form> 
        </div>
        <p class="suggest">建議配戴耳機遊玩，以獲得最佳遊戲體驗</p>
    </section>
</template>
<script>
export default {
    name: 'SectionStart',
    props:[],
    data(){
        return{
            bgmid:'bg01',
            stateid: 0,
            name: '',
            sectionid: 'meet'
        }
    },
    methods:{
        startgame(){
            this.stateid = 1;
            this.$parent.$emit('updatemusicid', 0);
        },
        sendname(e){
            e.preventDefault();//取消submit的預設動作
            this.$parent.$emit('updatename', this.name);
            this.$parent.$emit('updatesection', this.sectionid);
        }
    }
}
</script>
<style>
    #section-start{
        background-image: url(../assets/img/bg01.jpg);
    }
    #section-start .btn.btn-start{
        position: absolute;
        left: 50%;
        bottom: 92px;
        transform: translateX(-50%);
        background-image: url(../assets/img/btn01.png);
        background-color: rgba(0,0,0,0);
        border: none;
        width: 180px;
        height: 54px;
    }
    #section-start .suggest{
        position: absolute;
        color: #FFF;
        left: 50%;
        transform: translateX(-50%);
        bottom: 45px;
        text-shadow: 0 1px 7px #2d1400;
        letter-spacing: 1px;
        margin: 0;
        font-size: 20px;
    }
    #section-start .name{
        position: absolute;
        left: 345px;
        bottom: 100px;

    }
    #section-start .name #nameinput{
        position: relative;
        left: 30px;
        padding: 5px;
        font-size: 18px;
        border-radius: 90px;
        border: 1px solid #fcbf44;
        transition-duration: 0.3s;
    }
    #section-start .name #nameinput.send{
        left: 0px;
    }
    #section-start .name .btn.btn-name{
        position: relative;
        width: 44px;
        height: 36px;
        color: #FFFFFF;
        border-radius: 90px;
        background-image: url(../assets/img/btn_go.png);
        margin-left: 5px;
    }
    .fade-enter-active, .fade-leave-active {
        transition: opacity .3s;
        }
        .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
        }
</style>
