                    content:'通常是兩人以上的合奏，一個聲部由一位演奏家負責。最常見的有弦樂四重奏、鋼琴三重奏、鋼琴五重奏等編制。室內樂早期是在擁有高度鑑賞力的貴族沙龍內誕生的，許多作品本來就是為少數行家而寫；此後的室內樂也受這種歷史因素影響，成為作曲家最花腦筋的曲種，一首完整作品曲長通常就是一個半場；而由於室內樂對個別樂手的負擔比管弦樂團重得多，卻沒有來自樂團的穩定收入，演出室內樂的現實條件也較難達成。'
