<template>
    <section class="section-meet" v-bind:class="lan_file.talk1[i].backgroundimg">

        <transition  name="fade" mode="out-in">
            <div class="person" v-if="choosesex & datesex==''" key="a">
                <button class="boy" @click.prevent="sendsex('boy')"></button>
                <button class="girl" @click.prevent="sendsex('girl')"></button>
            </div>
            <div class="talkperson" v-else-if="datesex=='girl'" key="b">
                <img src="../assets/img/meet_girl.png" alt="">
            </div>
            <div class="talkperson" v-else-if="datesex=='boy'" key="c">
                <img src="../assets/img/meet_boy.png" alt="">
            </div>
        </transition>
        <transition  name="fade" mode="out-in">
            <div class="giveticket" v-if="ticket=='give'" key="d">
                <img src="../assets/img/bg03.jpg" alt="">
            </div>
            <div class="tickets" v-if="ticket=='choose'" key="e">
                <div class="tickets_inner">
                    <img :src="tic.imgurl" alt="" v-for="(tic,index) in tickets" :key="index" @click.prevent="sendconcert(index)" :class="{highlight:index == ticselected}" v-show=" index == ticselected || ticselected==5">
                </div>
            </div>
        </transition>

        <div class="talk-block">
            <div class="name-block">{{lan_file.talk1[i].name}}</div>
            <p>{{lan_file.talk1[i].content}}<span v-if="i==12 " key="f">{{this.concertname}}耶!</span></p>


            <transition  name="gonext" mode="out-in">
                <button id="next" @click="gonext" class="btn btn-talknext" v-show="talkblock">>></button>
            </transition>
        </div>
    </section>
</template>
<script>
    export default {
        name: 'SectionMeet',
        props: ['name','lan'],
        data(){

            return{
                talkblock: true,
                choosesex: false,
                ticket: '',
                datesex: '',
                sectionid:'intro',               
                concertname:"lower",
                i:0,
                ticselected: 5,
                tickets:[
                {imgurl:require('../assets/img/ticket01.jpg')},
                {imgurl:require('../assets/img/ticket02.jpg')},
                {imgurl:require('../assets/img/ticket03.jpg')},
                {imgurl:require('../assets/img/ticket04.jpg')}
                ],
                lan_ch:{
                    talk1:[
                    {
                        name: `${this.name}`,
                        content: '（心儀對象也差不多快下課了吧，不知道會不會遇到他呢？）',
                        backgroundimg: 'blur'
                    },
                    {
                        name: '???',
                        content: `${this.name}～${this.name}～`,
                        backgroundimg: 'blur'
                    },
                    {
                        name: `${this.name}`,
                        content: `聽到有人從背後呼喚我，是誰呢？`,
                        backgroundimg: 'blur'
                    },{
                        name: '系統提示',
                        content: '選擇你的心儀對象',
                        backgroundimg: 'blur'
                    },{
                        name: '心儀的對象',
                        content: '嘿，你在這幹嘛呀!?',
                        backgroundimg: ''
                    },{
                        name: `${this.name}`,
                        content: '沒......沒有啊！我只是剛好經過這裡',
                        backgroundimg: ''
                    },{
                        name: '心儀的對象',
                        content: '這樣啊。那你等等有空嗎？要不要陪我去聽音樂會',
                        backgroundimg: ''
                    },{
                        name: `${this.name}`,
                        content: '音樂會嗎？但我從來沒去過耶......',
                        backgroundimg: ''
                    },{
                        name: '心儀的對象',
                        content: '別擔心，其實很輕鬆，跟看電影一樣，不用太嚴肅喔',
                        backgroundimg: ''
                    },{
                        name: '心儀的對象',
                        content: '這邊有幾場不同類型音樂會的票，看看你想聽哪一種吧！',
                        backgroundimg: ''
                    },{
                        name: `${this.name}`,
                        content: '(哇！竟然是四場選一場，這麼好！那我要選想聽的類型！)',
                        backgroundimg: ''
                    },{
                        name: '心儀的對象',
                        content: `這場嗎？`,
                        backgroundimg: ''
                    },{
                        name: '心儀的對象',
                        content: '真巧！我也想聽',
                        backgroundimg: ''
                    },{
                        name: `${this.name}`,
                        content: '可是...我真的沒聽過音樂會喔...？沒關係嗎？',
                        backgroundimg: ''
                    },{
                        name: '心儀的對象',
                        content: '沒關係，我會手把手的教你的',
                        backgroundimg: ''
                    },{
                        name: `${this.name}`,
                        content: '(好，那我這次約會一定要好好表現！)',
                        backgroundimg: ''
                    },{
                        name: '心儀的對象',
                        content: '那我們就走吧！',
                        backgroundimg: ''
                    }
                    ]
                },
                lan_en:{
                    talk1:[
                    {
                        name: `${this.name}`,
                        content: 'EN（心儀對象也差不多快下課了吧，不知道會不會遇到他呢？）',
                        backgroundimg: 'blur'
                    },
                    {
                        name: '???',
                        content: `EN${this.name}～${this.name}～`,
                        backgroundimg: 'blur'
                    },
                    {
                        name: `${this.name}`,
                        content: `EN聽到有人從背後呼喚我，是誰呢？`,
                        backgroundimg: 'blur'
                    },{
                        name: '系統提示',
                        content: 'EN選擇你的心儀對象',
                        backgroundimg: 'blur'
                    },{
                        name: '心儀的對象',
                        content: 'EN嘿，你在這幹嘛呀!?',
                        backgroundimg: ''
                    },{
                        name: `${this.name}`,
                        content: 'EN沒......沒有啊！我只是剛好經過這裡',
                        backgroundimg: ''
                    },{
                        name: '心儀的對象',
                        content: 'EN這樣啊。那你等等有空嗎？要不要陪我去聽音樂會',
                        backgroundimg: ''
                    },{
                        name: `${this.name}`,
                        content: 'EN音樂會嗎？但我從來沒去過耶......',
                        backgroundimg: ''
                    },{
                        name: '心儀的對象',
                        content: 'EN別擔心，其實很輕鬆，跟看電影一樣，不用太嚴肅喔',
                        backgroundimg: ''
                    },{
                        name: '心儀的對象',
                        content: 'EN這邊有幾場不同類型音樂會的票，看看你想聽哪一種吧！',
                        backgroundimg: ''
                    },{
                        name: `${this.name}`,
                        content: 'EN(哇！竟然是四場選一場，這麼好！那我要選想聽的類型！)',
                        backgroundimg: ''
                    },{
                        name: '心儀的對象',
                        content: `EN這場嗎？`,
                        backgroundimg: ''
                    },{
                        name: '心儀的對象',
                        content: 'EN真巧！我也想聽',
                        backgroundimg: ''
                    },{
                        name: `${this.name}`,
                        content: 'EN可是...我真的沒聽過音樂會喔...？沒關係嗎？',
                        backgroundimg: ''
                    },{
                        name: '心儀的對象',
                        content: 'EN沒關係，我會手把手的教你的',
                        backgroundimg: ''
                    },{
                        name: `${this.name}`,
                        content: 'EN(好，那我這次約會一定要好好表現！)',
                        backgroundimg: ''
                    },{
                        name: '心儀的對象',
                        content: 'EN那我們就走吧！',
                        backgroundimg: ''
                    }
                    ]
                }

            }
        },
        methods:{
            gonext(){
                if(this.i < this.lan_file.talk1.length-1){
                    this.i++;
                    // console.log(this.lan_file.talk1.length+'/click next'+ this.i);
                }else{
                    this.$parent.$emit('updatesection', this.sectionid);
                }
                var vobj=this;
                switch (this.i){
                    case 3:

                    this.talkblock = false;
                    this.choosesex = true;
                    break;
                    case 9:
                    this.ticket ="give";
                    console.log(this.ticket);
                    break;
                    case 10:
                    this.talkblock = false;
                    this.ticket ="choose";
                    console.log(this.ticket);                      
                    break;

                    case 13:
                    this.talkblock = false;
                    this.ticket ="";
                    console.log(this.ticket);
                    
                    setTimeout(function(){vobj.talkblock =true; }, 0);//1000
                    break;

                    default:
                    this.talkblock = false;

                    setTimeout(function(){vobj.talkblock =true; }, 0);//1000
                    break;

                }


            },
            sendsex(sex){
                this.datesex = sex;
                this.$parent.$emit('updatesex', this.datesex);
                this.$parent.$emit('updatemusicid', 1);
                this.i++;
                this.talkblock = true;
            },
            sendconcert(type){
                if(this.ticselected !== type){
                    switch(type){
                        case 0:
                        this.concertname='室內樂'
                        break;
                        case 1:
                        this.concertname='交響曲'
                        break;
                        case 2:
                        this.concertname='協奏曲'
                        break;
                        case 3:
                        this.concertname='鋼琴獨奏'
                        break;
                    }
                    this.ticselected = type;
                    this.$parent.$emit('updateconcert', type+1);                    
                    this.i++;
                    this.talkblock = false;
                    var vobj =this;
                    setTimeout(function(){ vobj.i++; vobj.talkblock =true; }, 0);//1000
                    console.log("choosed:"+this.concertname);

                }else{
                    console.log('choose done')
                }

            }
        },
        computed:{
            lan_file:function(){
                console.log("lanfile"+this.con);
                if(this.lan =="ch"){
                    return this.lan_ch;
                }else{
                    return this.lan_en;
                }
            }
        }
    }
</script>
<style>
.section-meet{
    background-image: url(../assets/img/bg02.jpg);
}
.section-meet.blur{
    background-image: url(../assets/img/bg02blur.jpg);
}
.talk-block{
    position: absolute;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    width: 800px;
    height: 90px;
    padding: 10px 20px;
    padding-right: 100px;
    background-color: rgba(43, 22, 22, 0.6);
    z-index: 10;
}
.talk-block .name-block{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: -45px;
    left: 0;
    background-color: rgba(43, 22, 22, 0.6);
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    min-width: 200px;
    height: 45px;
    padding: 0 20px;
    font-size: 24px;
    color: #FFF;
    text-align: center;
    border-bottom: 1px solid #584343;
    z-index: 10;
}
.talk-block p{
    margin: 0;
    font-size: 24px;
    color: #FFF;
}
.talk-block .btn-talknext{
    position: absolute;
    height: 100%;
    bottom: 0;
    right: 0;
    width: 100px;
    background-color: rgba(255,255,255,.8);
}
.section-meet .person button{
    background-size: contain;
    position: absolute;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    transition-duration: 0.3s;
}
.section-meet .person button.boy{
    bottom: 0;
    left: 130px;
    width: 325px;
    height: 434px;
    background-image: url(../assets/img/dark_boy.png);
}
.section-meet .person button.girl{
    bottom: 0;
    right: 130px;
    width: 370px;
    height: 434px;
    background-image: url(../assets/img/dark_girl.png);
}
.section-meet .talk-person{
    position: absolute;
}
.section-meet .giveticket{
    position: absolute;
    top: 0;
    left: 0;
}
.section-meet .tickets{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.4);
}
.section-meet .tickets .tickets_inner{
    width: 855px;
    height: 350px;
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
}
.section-meet .tickets .tickets_inner img{
    position: absolute;
    width: 400px;
    height: 160px;
    cursor: pointer;
    box-shadow: 0 5px 20px -5px #000;
    border-top: 1px solid #828282;
    border-left: 1px solid #dbdbdb;
    border-radius: 5px;
    transition-duration: 0.3s;
}
.section-meet .tickets .tickets_inner img.highlight,
.section-meet .tickets .tickets_inner img.highlight:hover,
.section-meet .tickets .tickets_inner img.highlight:active{
    transform: scale(1.5)translate(-50%,-50%)!important;
    top: 50%!important;
    left: 50%!important;
    transform-origin: left top;
}
.section-meet .tickets .tickets_inner img:nth-child(1){
    top: 0;
    left: 0;
}
.section-meet .tickets .tickets_inner img:nth-child(2){
    top: 0;
    left: 100%;
    transform: translateX(-100%);
}
.section-meet .tickets .tickets_inner img:nth-child(3){
    left: 0;
    top: 100%;
    transform: translateY(-100%);
}
.section-meet .tickets .tickets_inner img:nth-child(4){
    top: 100%;
    left: 100%;
    transform: translate(-100%,-100%);
}
.section-meet .tickets .tickets_inner img:hover,.section-meet .tickets .tickets_inner img:active{
    box-shadow: 0 5px 20px -5px rgb(255, 255, 255);
}
.section-meet .tickets .tickets_inner img:nth-child(1):hover,.section-meet .tickets .tickets_inner img:nth-child(1):active{
    box-shadow: 0 5px 20px -5px rgb(255, 255, 255);
    transform: scale(1.05);
}
.section-meet .tickets .tickets_inner img:nth-child(2):hover,.section-meet .tickets .tickets_inner img:nth-child(2):active{
    box-shadow: 0 5px 20px -5px rgb(255, 255, 255);
    transform: scale(1.05)translateX(-95%);
}
.section-meet .tickets .tickets_inner img:nth-child(3):hover,.section-meet .tickets .tickets_inner img:nth-child(3):active{
    box-shadow: 0 5px 20px -5px rgb(255, 255, 255);
    transform: scale(1.05)translateY(-95%);
}
.section-meet .tickets .tickets_inner img:nth-child(4):hover,.section-meet .tickets .tickets_inner img:nth-child(4):active{
    box-shadow: 0 5px 20px -5px rgb(255, 255, 255);
    transform: scale(1.05)translate(-95%,-95%);
}

.gonext-enter-active, .gonext-leave-active {
  transition: opacity .2s ease;
}
.gonext-enter{
    opacity: 0;
}
.gonext-enter-to{
    display: block;
}
.gonext-leave{
    opacity: 1;
}
.gonext-leave-to{
    display: none;
}
</style>