<template>
    <div>
        真D載入中:D
    </div>
</template>
<script>
export default {
    name:'loadsrc',
    data(){
        return{
            imgcount:0,
            sectionid:'start',
            imgarray:[
            {src:require('../assets/img/bg01.jpg')},
            {src:require('../assets/img/bg02.jpg')},
            {src:require('../assets/img/bg02blur.jpg')},
            {src:require('../assets/img/bg03.jpg')},
            {src:require('../assets/img/btn01.png')},
            {src:require('../assets/img/meet_boy.png')},
            {src:require('../assets/img/meet_girl.png')},
            {src:require('../assets/img/light_boy.png')},
            {src:require('../assets/img/light_girl.png')},
            {src:require('../assets/img/shadow_boy.png')},
            {src:require('../assets/img/shadow_girl.png')},
            {src:require('../assets/img/ticket01.jpg')},
            {src:require('../assets/img/ticket02.jpg')},
            {src:require('../assets/img/ticket03.jpg')},
            {src:require('../assets/img/ticket04.jpg')}
            ]
        }
    },
    mounted(){
 
            for(let i=0;i<this.imgarray.length;i++) {
                var img = new Image();
                var vobj = this;
                img.onload = (function () {
                    return function() {
                        vobj.imgcount++;
                        console.log(vobj.imgcount)
                    }
                }(i));      
                img.src = this.imgarray[i].src;
            }
    },
    watch:{
        imgcount:function(){
            if(this.imgcount > this.imgarray.length -1){
                console.log('loop done');
                this.$parent.$emit('updatesection', this.sectionid);
            }
        }
    }
    
}
</script>